.c-resp {
	// 각 너비별로 hide
	&-mobile-hide {
		@include mixin-respond(mobile) {display: none !important;}
	}
	&-tablet-hide {
		@include mixin-respond(tablet) {display: none !important;}
	}
	&-desktop-hide {
		@include mixin-respond(desktop) {display: none !important;}
	}
  &-mobile-tablet-hide {
		@include mixin-respond(mobile_tablet) {display: none !important;}
	}
	&-tablet-desktop-hide {
		@include mixin-respond(tablet_desktop) {display: none !important;}
	}
    
	
	&-img {
		display: block;
		max-width: 100%;
		height: auto;
	}
}

.sss-c-print {
	&-none {
		@include mixin-respond(print) {
			display: none;
		}
	}
}