.gnb {
  &._g-1 {
		$grid: 'gnb_g-1_';
		
		grid-template-columns: 1fr auto;
		grid-template-rows: auto;
		grid-template-areas: "#{$grid}1 #{$grid}2";
		
		@include mixin-gd_item($grid, 2);
	}
	&._gd-2 {
		$grid: 'gnb_gd-2_';

		@include mixin-respond(mobile_tablet) {
			grid-template-columns: 115px 1fr auto;
			grid-template-rows: 79px;
			grid-template-areas: "#{$grid}1 #{$grid}2 #{$grid}3";
		}
		
		@include mixin-respond(desktop) {
			grid-template-columns: 1fr;
			grid-template-rows: auto 1fr auto;
			grid-template-areas: "#{$grid}1" "#{$grid}2" "#{$grid}3";
		}
		
		@include mixin-gd_item($grid, 3);
	}
	
	&._area {
		&-1 {
      
      @include mixin-respond(mobile_tablet) {
        $val: 14px;

        padding-left: $val;
        padding-right: $val;
      }
      @include mixin-respond(desktop) {
        $val: 20px;

        padding-left: $val;
        padding-right: $val;
        padding-top: 56px;
        padding-bottom: $val;
      }
		}
		&-2 {
			$val: 0.4em;
			margin-top: $val;
			margin-bottom: $val;
    }
    &-3 {
			@include mixin-respond(mobile_tablet) { padding-top: 20px; }
			@include mixin-respond(desktop) { padding-top: 58px; }
			
      padding-bottom: 20px;
		}
		&-4 {
			padding-left: 14px;
			padding-right: 14px;
		}
		&-5 {
			position: absolute;
			width: 65vw;
			top: 79px;
			right: 0px;
			z-index: 1;
		}
	}
	
	&[data-menu-stat] {
		transition-property: transform;
		transition-duration: .2s;
	}
	&[data-menu-stat="more"] {
		transform: rotate(-180deg);
	}
}