.c-gd {
	display: grid;
	
	&-vc {
		align-self: center;
	}
	&-vs {
		align-self: start;
	}
	&-ve {
		align-self: end;
	}
	&-hs {
		justify-self: start;
	}
	&-hc {
		justify-self: center;
	}
	&-he {
		justify-self: end;
	}
	&-cc {
		justify-self: center;
		align-self: center;
	}
}


.c-flex {
	display: flex;
	
	&-cc {
		justify-content: center;
		align-items: center;
	}
	&-hsb {
		justify-content: space-between;
	}
	&-he {
		justify-content: flex-end;
	}
	&-hc {
		justify-content: center;
	}
	&-vc {
		align-items: center;
	}
	&-wrap {
		flex-wrap: wrap;
	}
}




.c-dp {
	&-block {
		display: block;
	}
	&-none {
		display: none !important;
	}
	&-inlineblock {
		display: inline-block;
	}
	&-table {
		display: table;
		border-collapse: collapse;
		// table-layout: fixed;
		// width: 100%;
	}
	&-tablefix {
		display: table;
		border-collapse: collapse;
		table-layout: fixed;
		width: 100%;
	}
	&-tablecell {
		display: table-cell;
	}
}


.sss-c-visibility {
	&-hidden {
		visibility: hidden;
	}
}

.c-align {
	&-top {
		vertical-align: top;
	}
	&-middle {
		vertical-align: middle;
	}
	&-left {
		text-align: left;
	}
	&-right {
		text-align: right;
	}
	&-c {
		text-align: center;
	}
}


.sss-c-position {
	&-absolute {
		position: absolute;
	}
	
	&-relative {
		position: relative;
	}
	
	&-fixed {
		position: fixed;
	}
}


.c-backdrop {
	&:after {
		@include mixin-respond(mobile_tablet) {
			content: "";
			position: fixed;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			background-color: rgba(0,0,0,0.5);
		}
	}
}

.c-zx-1 {
	z-index: 1;
}