@font-face {
	font-family: "nanum";
	src: url("/assets/font/NanumGothic-Regular.woff2") format("woff2"), url("/assets/font/NanumGothic-Regular.woff") format("woff");
	font-weight: normal;
}

@font-face {
	font-family: "nanum";
	src: url("/assets/font/NanumGothic-Bold.woff2") format("woff2"), url("/assets/font/NanumGothic-Bold.woff") format("woff");
	font-weight: bold;
}


.c-font-bold {
	font-weight: bold;
}


.c-font-size {
	&-14 {font-size: 14px;}
	&-16 {font-size: 16px;}
	&-18 {font-size: 18px;}
	&-21 {font-size: 21px;}
	&-24 {font-size: 24px;}
}