.lp {
	&._gd {
		&-0 {
			$grid: 'lp_gd-0_';
			
			grid-template-columns: 100%;
			grid-template-rows: minmax(100vh, 1fr);
			grid-template-areas: "#{$grid}1";
			
			@include mixin-gd_item($grid, 1);
		}
	}
	
	&._gd-1 {
		$grid: 'lp_gd-1_';

		@include mixin-respond(mobile_tablet) {
			grid-template-columns: 100%;
			grid-template-rows: 79px 1fr auto;
			grid-template-areas: "#{$grid}1" "#{$grid}2" "#{$grid}3";
		}
		@include mixin-respond(desktop) {
			grid-template-columns: 270px minmax(1px, 1fr);
			grid-template-rows: minmax(100vh, 1fr);
			grid-template-areas: "#{$grid}1 #{$grid}2";
		}
		
		@include mixin-gd_item($grid, 3);
	}
}