$c-color-1: rgb(0,0,0);
$c-color-2: rgb(181, 105, 73);
$c-color-3: rgb(45, 54, 145);
$c-color-4: rgb(255, 255, 255);
$c-color-5: rgb(200, 200, 200);

.c-bgcolor {
	&-1 {
		background-color: rgb(48,48,48);
	}
}

.c-color {
	&-1 {color: $c-color-1;}
	&-2 {color: $c-color-2;}
	&-3 {color: $c-color-3;}
	&-4 {color: $c-color-4;}
	&-5 {color: $c-color-5;}
}