.about {
	&._g {
		&-1 {
			$grid: 'about_g-1_';
	
			@include mixin-respond(mobile) {
				grid-row-gap: 30px;
				grid-column-gap: 10px;
				grid-template-columns: 1fr 1fr;
				grid-template-rows: auto auto auto auto auto;
				grid-template-areas: "#{$grid}1 #{$grid}2" "#{$grid}3 #{$grid}4" "#{$grid}5 #{$grid}6" "#{$grid}7 #{$grid}8" "#{$grid}9 #{$grid}10";
			}
			@include mixin-respond(tablet) {
				grid-row-gap: 40px;
				grid-column-gap: 30px;
				grid-template-columns: 1fr 1fr 1fr;
				grid-template-rows: auto auto auto auto;
				grid-template-areas: "#{$grid}1 #{$grid}2 #{$grid}3" "#{$grid}4 #{$grid}5 #{$grid}6" "#{$grid}7 #{$grid}8 #{$grid}9" "#{$grid}10 . .";
			}
			@include mixin-respond(desktop) {
				grid-row-gap: 40px;
				grid-column-gap: 20px;
				grid-template-columns: 1fr 1fr 1fr 1fr;
				grid-template-rows: auto auto auto;
				grid-template-areas: "#{$grid}1 #{$grid}2 #{$grid}3 #{$grid}4"
					"#{$grid}5 #{$grid}6 #{$grid}7 #{$grid}8" 
					"#{$grid}9 #{$grid}10 . .";
			}
	
			@include mixin-gd_item($grid, 10); 
    }
    
		&-2 {
			$grid: 'about_g-2_';
		
      grid-template-columns: 1fr;
      grid-template-rows: auto auto;
      grid-template-areas: "#{$grid}1" "#{$grid}2";

      @include mixin-gd_item($grid, 2); 
		}
  }
}