$fontSizeStandard: 10px;


@function px2rem($n){
	@return ($n / $fontSizeStandard) * 1rem;
}


@mixin mixin-respond($media) {
	@if $media == mobile {
    @media (max-width: 767px) { @content; }
	}
	@else if $media == tablet {
		@media (min-width: 768px) and (max-width: 1024px) { @content; }
	}
	@else if $media == desktop {
        @media (min-width: 1025px) { @content; }
	}
	@else if $media == mobile_tablet {
		@media (max-width: 1024px) { @content; }
	}
	@else if $media == tablet_desktop {
		@media (min-width: 768px) { @content; }
	}
	@else if $media == print {
		@media print { @content; }
	}
	@else {
		@error "#{$media} value is wrong";
	}
}

@mixin mixin-gd_item($grid, $length){
	@for $i from 1 through $length {
		> [data-gd="#{$i}"] {
			grid-area: #{$grid}#{$i};
		}
	}
}