.pl {
  $grid_column: 18px;

	&._gd-2 {
    $grid: 'pl_gd-2_';

    @include mixin-respond(mobile_tablet) {
      grid-column-gap: $grid_column;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto;
      grid-template-areas: "#{$grid}1 #{$grid}2"
        "#{$grid}3 #{$grid}4"
        "#{$grid}5 #{$grid}6"
        "#{$grid}7 #{$grid}8"
        "#{$grid}9 #{$grid}10"
        "#{$grid}11 #{$grid}12"
        "#{$grid}13 #{$grid}14"
        "#{$grid}15 #{$grid}16"
        "#{$grid}17 #{$grid}18"
        "#{$grid}19 #{$grid}20"
        "#{$grid}21 #{$grid}22"
        "#{$grid}23 #{$grid}24"
        "#{$grid}25 #{$grid}26"
        "#{$grid}27 #{$grid}28"
        "#{$grid}29 #{$grid}30"
        "#{$grid}31 #{$grid}32"
        "#{$grid}33 #{$grid}34"
        "#{$grid}35 #{$grid}36"
        "#{$grid}37 #{$grid}38"
        "#{$grid}39 #{$grid}40";
    }
    @include mixin-respond(desktop) {
      grid-column-gap: 10px;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: auto auto auto auto auto auto auto auto auto auto auto auto auto auto;
      grid-template-areas: 
      "#{$grid}1 #{$grid}2 #{$grid}3"
      "#{$grid}4 #{$grid}5 #{$grid}6"
      "#{$grid}7 #{$grid}8 #{$grid}9"
      "#{$grid}10 #{$grid}11 #{$grid}12"
      "#{$grid}13 #{$grid}14 #{$grid}15"
      "#{$grid}16 #{$grid}17 #{$grid}18"
      "#{$grid}19 #{$grid}20 #{$grid}21"
      "#{$grid}22 #{$grid}23 #{$grid}24"
      "#{$grid}25 #{$grid}26 #{$grid}27"
      "#{$grid}28 #{$grid}29 #{$grid}30"
      "#{$grid}31 #{$grid}32 #{$grid}33"
      "#{$grid}34 #{$grid}35 #{$grid}36"
      "#{$grid}37 #{$grid}38 #{$grid}39"
      "#{$grid}40	.	.";
    }

    @include mixin-gd_item($grid, 40);
  }
		
  &._gd-3 {
    $grid: 'pl_gd-3_';
    
    // grid-row-gap: 0px;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto 1fr;
    grid-template-areas: "#{$grid}1" "#{$grid}2" "#{$grid}3";
    
    @include mixin-gd_item($grid, 3);

    margin-bottom: 13px;
  }
		
	&._gd-4 {
    $grid: 'pl_gd-4_';
    
    @include mixin-respond(mobile_tablet) {
      grid-template-columns: 1fr;
      grid-template-rows: auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto;
      grid-template-areas: "#{$grid}1" "#{$grid}2" "#{$grid}3" "#{$grid}4" "#{$grid}5" "#{$grid}6" "#{$grid}7" "#{$grid}8" "#{$grid}9" "#{$grid}10" "#{$grid}11" "#{$grid}12" "#{$grid}13" "#{$grid}14" "#{$grid}15" "#{$grid}16" "#{$grid}17" "#{$grid}18" "#{$grid}19" "#{$grid}20" "#{$grid}21" "#{$grid}22" "#{$grid}23" "#{$grid}24" "#{$grid}25" "#{$grid}26" "#{$grid}27" "#{$grid}28" "#{$grid}29" "#{$grid}30" "#{$grid}31" "#{$grid}32" "#{$grid}33" "#{$grid}34" "#{$grid}35" "#{$grid}36" "#{$grid}37" "#{$grid}38" "#{$grid}39" "#{$grid}40";
    }
    @include mixin-respond(desktop) {
      grid-column-gap: $grid_column;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto;
      grid-template-areas: "#{$grid}1 #{$grid}2"
        "#{$grid}3 #{$grid}4"
        "#{$grid}5 #{$grid}6"
        "#{$grid}7 #{$grid}8"
        "#{$grid}9 #{$grid}10"
        "#{$grid}11 #{$grid}12"
        "#{$grid}13 #{$grid}14"
        "#{$grid}15 #{$grid}16"
        "#{$grid}17 #{$grid}18"
        "#{$grid}19 #{$grid}20"
        "#{$grid}21 #{$grid}22"
        "#{$grid}23 #{$grid}24"
        "#{$grid}25 #{$grid}26"
        "#{$grid}27 #{$grid}28"
        "#{$grid}29 #{$grid}30"
        "#{$grid}31 #{$grid}32"
        "#{$grid}33 #{$grid}34"
        "#{$grid}35 #{$grid}36"
        "#{$grid}37 #{$grid}38"
        "#{$grid}39 #{$grid}40";
    }

    @include mixin-gd_item($grid, 40);
  }
}